import { User } from '@/types/Type'
import { defineStore } from 'pinia'
import { computed, ref } from 'vue'

export const useUserDetailsStore = defineStore('userDetails', () => {
  const searchedKeywords = ref<string[]>([])
  const userData = ref<User[]>([])
  const individualUserData = ref<User>()
  const accessToIndividualUserData = ref<boolean>(false)

  const getKeywords = computed(() => searchedKeywords.value.slice(-4))
  const getLastKeyword = computed(() => searchedKeywords.value[searchedKeywords.value.length -1] || '')
  const getUserData = computed(() => userData.value)
  const getIndividualUserData = computed(() => individualUserData.value)
  const getAccessToIndividualUserData = computed(() => accessToIndividualUserData.value)

  const setUsersData = (recievedUsers:User[]) => userData.value = recievedUsers

  const addSearchedKeyword = (keyword:string) => {
    if(!searchedKeywords.value.includes(keyword)) {
      searchedKeywords.value.push(keyword)
      
      return 'done'
    } else {
      return 'fail'
    }
  }

  const removeSearchedKeyword = (toRemoveKeyword:string) => {
    searchedKeywords.value = searchedKeywords.value.filter((searchedKeyword:string) => toRemoveKeyword !== searchedKeyword)
  }    

  const setIndividualUserData = (userData:User) => {
    individualUserData.value = userData
  }

  const setAccessToIndividualUserData = (shouldAllow:boolean) => {
    accessToIndividualUserData.value = shouldAllow
  }

  return {
    getKeywords, 
    getLastKeyword, 
    getUserData, 
    getIndividualUserData, 
    getAccessToIndividualUserData, 
    setUsersData,
    addSearchedKeyword,
    removeSearchedKeyword,
    setIndividualUserData,
    setAccessToIndividualUserData,
  }
})